import { sendMessage, checkMobile } from "@/api/login";
const second = 4;
export default {
  data() {
    var checkphone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("手机号不能为空"));
      } else if (!/^1[0-9]{10}$/.test(this.form.phone)) {
        callback(new Error("手机号格式不正确"));
      } else {
        callback();
      }
    };
    var checkCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("验证码不能为空"));
      } else {
        callback();
      }
    };
    return {
      disabled: false,
      time: "",
      timer: null,
      form: {
        code: "",
        phone: ""
      },
      rules: {
        phone: [{ validator: checkphone, trigger: "blur" }],
        code: [{ validator: checkCode, trigger: "blur" }]
      }
    };
  },
  computed: {
    text: function() {
      if (this.time == second) {
        return "获取验证码";
      }
      return this.time > 0 ? this.time + "s后重新获取" : "获取验证码";
    }
  },
  created() {},
  methods: {
    run: function() {
      let _this = this;
      _this.time = second;
      let timer = setInterval(function() {
        _this.disabled = true;
        if (_this.time-- == 0) {
          clearInterval(timer);
          _this.time = second;
          _this.disabled = false;
          return _this.time;
        }
      }, 1000);
    },
    sendMessage: function(type) {
      if (!/^1[0-9]{10}$/.test(this.form.phone)) {
        this.$message.error("手机号格式不正确");
        return;
      }
      if (type == 1) {
        checkMobile(this.form.phone).then(res => {
          if (res.data.code == 0) {
            this.$message.error("此手机号未注册此平台");
            return;
          } else {
            this.run();
            sendMessage(this.form.phone).then(res => {
              if (res.data.code == 1) {
                this.$message.success(res.data.msg);
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        });
      } else {
        this.run();
        sendMessage(this.form.phone).then(res => {
          if (res.data.code == 1) {
            this.$message.success(res.data.msg);
          } else {
            this.$message.error(res.data.msg);
          }
        });
      }
    }
  }
};



