<template>
    <div> 
        <el-upload 
             class="video-uploader" 
            :action="uploadUrl" 
            :show-file-list="false" 
            :on-success="handleVideoSuccess" 
            :before-upload="beforeUploadVideo" 
            :on-progress="uploadVideoProcess">  

              <video v-show="videoData" :src="videoData" controls="controls" width="160" height="100">您的浏览器不支持视频播放</video>
              <i v-show="!videoData" class="el-icon-plus avatar-uploader-icon"></i>
              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>可上传swf,mp4,avi格式</span> 
        </el-upload>
       
    </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      uploadUrl:
        process.env.BASE_API.split("/agent")[0] + "/admin/Common/uploadimg",
      videoFlag: false,
      videoData: this.data
    };
  },
  methods: {
    beforeUploadVideo(file) {
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (
        [
          "video/mp4",
          "video/ogg",
          "video/flv",
          "video/avi",
          "video/wmv",
          "video/rmvb"
        ].indexOf(file.type) == -1
      ) {
        this.$message.error("请上传正确的视频格式");
        return false;
      }
      if (!isLt10M) {
        this.$message.error("上传视频大小不能超过10MB哦!");
        return false;
      }
    },
    uploadVideoProcess(event, file, fileList) {
      this.videoFlag = true; 
      this.videoData = URL.createObjectURL(file.raw);
    },
    handleVideoSuccess(res, file) {
      this.videoFlag = false;
      this.$emit("onSuccess", res.src); 
      this.$message.success("视频上传成功！");
    }
  }
};
</script>

<style lang='scss' scoped>

.video-uploader {
  position: relative;
}
.video-uploader i.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 80px;
  height: 80px;
  line-height: 80px;
}  

</style>



