import request from '@/utils/request'

export function getCommunity(title) {
  return request({
    url: "/broker/getCommunity",
    method: "get",
    params: {
      title: title
    }
  });
} 


/* 小区 */
export function addCommunity(data) {
  return request({
    url: '/broker/addCommunity',
    method: 'post',
    data
  })
}


export function getCommunityParam() {
  return request({
    url: '/broker/getCommunityParam',
    method: 'get',
  })
}

export function getCommunityList(params) {
  return request({
    url: '/broker/getCommunityList',
    method: 'get',
    params
  })
}


/* 二手房 */
export function salePublish(method,params,data) {
  return request({
    url: '/broker/salePublish' + (params ? '?id=' + params : ''),
    method: method,
    data
  })
}

export function getSaleRentShopList(params) {
  return request({
    url: '/broker/getSaleRentShopList',
    method: 'get',
    params
  })
}


export function operateSaleRentShop(params) {
  return request({
    url: '/broker/operateSaleRentShop',
    method: 'get',
    params
  })
}



/* 商铺 */
export function shopPublish(method,params,data) {
  return request({
    url: '/broker/shopPublish' + (params ? '?id=' + params : ''),
    method: method,
    data
  })
}


// 租房 
export function rentPublish (method,params,data) {
  return request({
    url: '/broker/rentPublish' + (params ? '?id=' + params : ''),
    method: method,
    data
  })
}
 
 


 


