<template>
    <div class="uploadImgs">
      <ul class="el-upload-list el-upload-list--picture-card fl">
        <li v-if="item" tabindex="0" class="el-upload-list__item is-success" v-for="(item,index) in staticLists"> 
          <img :src="item" alt="" class="el-upload-list__item-thumbnail"> 
          <span class="el-upload-list__item-actions">
            <span class="el-upload-list__item-preview"><i class="el-icon-zoom-in" @click="handlePictureCardPreview(1,item)"></i>
            </span>
            <span class="el-upload-list__item-delete"><i class="el-icon-delete" @click="delteImg(item)"></i>
            </span>
          </span>
        </li>
    </ul>
       
        <el-upload
            style="float: left"
            :action="url"  
            accept=".jpg,.gif,.png"  
            list-type="picture-card"
            :file-list='fileList'
            :data="upLoadData"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :onSuccess="handleAvatarSuccess"
            :limit=10>
            <i class="el-icon-plus"></i>
        </el-upload>
        <span class="public_littleTip"><i class="iconfont">&#xe670;</i>多图上传，最多传10张</span>
        <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
    </div>
</template>
<script>
export default {
  props: ["data"],
  data() {
    return {
      url: process.env.BASE_API.split("/agent")[0] + "/admin/Common/uploadimg",
      dialogImageUrl: "",
      dialogVisible: false,
      fileList: [],
      staticLists: this.data,
      imgList: [],
      upLoadData: {} // 上传时附带的额外参数
    };
  },
  created() {},
  methods: {
    filter(fileList) {
      this.imgList = [];
      fileList.forEach(item => {
        this.imgList.push(item.response.src);
      });
      this.$emit("onSuccess", this.staticLists.concat(this.imgList));
    },
    delteImg(val) {
      this.staticLists = this.staticLists.filter(item => {
        return item != val;
      });
      this.$emit("onSuccess", this.staticLists.concat(this.imgList));
    },
    /* 删除 */
    handleRemove(file, fileList) {
      this.filter(fileList);
    },
    /* 预览 */
    handlePictureCardPreview(type, file) {
      if (type == 1) {
        this.dialogImageUrl = file;
      } else {
        this.dialogImageUrl = file.url;
      }

      this.dialogVisible = true;
    },
    //上传成功刷新并通知父组件
    handleAvatarSuccess(res, file, fileList) {
      this.loading = false;

      this.filter(fileList);
      this.$message({
        message: "图片上传成功",
        type: "success"
      });
    }
  }
};
</script>

<style>
.imgLists {
  cursor: pointer;
  float: left;
  margin-right: 5px;
}
.el-upload--picture-card {
  width: 80px !important;
  height: 80px !important;
  background-color: #fff !important;
}
.el-upload--picture-card:hover, .el-upload:focus {
  border-color: #d9d9d9 !important;
  color: #d9d9d9 !important;
}
.uploadImgs .public_littleTip {
  position: relative;
  top: 50px;
}
.el-upload-list--picture-card .el-upload-list__item {
  width: 80px !important;
  height: 80px !important;
}
.uploadImgs .el-icon-plus {
  position: relative;
  top: -30px;
}
</style>



