<template>
    <el-form ref="form" :model="form" :rules="rules" :inline="true">
        <el-form-item :label="label" prop="phone">
            <el-input v-model.number.trim="form.phone" style="width: 140px;"></el-input>
           
            <el-button type="primary" round size="small" class="getcode" @click="sendMessage" :disabled="disabled">{{ text }}</el-button>
            </el-form-item>
            
        <el-form-item prop="code">
            <el-input v-model.trim="form.code" style="width: 140px;"></el-input>
        </el-form-item>
        
    </el-form>
</template>

<script>
import mixin from "../../mixin/index";
export default {
  mixins: [mixin],
  props: {
    label: {
      type: String
    }
  },
  data() {
    return {};
  },

  methods: {}
};
</script>

<style lang='scss' scoped>
.getcode {
  margin-left: 20px;
}
.code_success {
  margin-left: 20px;
  color: #304156;
  i {
    color: #34bfa3;
    margin-right: 6px;
  }
}
.code_error {
  margin-left: 20px;
  color: #f44864;
  i {
    color: #f44864;
    margin-right: 6px;
  }
}
.tip {
  font-size: 12px;
  margin-left: 10px;
  color: rgba(153, 153, 153, 1);
  i {
    margin-right: 6px;
  }
}
</style>



