<template>
<div>
	<!--图片上传-->
	<el-upload
  :style="{ width: width, height: height, lineHeight: height}"
    class="avatar-uploader"
    
		:action="url"
		:limit='limit'
		accept=".jpg,.gif,.png"
		:data="upLoadData"
		:file-list='fileList'
		:before-upload="beforeAvatarUpload"
		:onSuccess="handleAvatarSuccess"
		:show-file-list=false>


     
    <div v-if="!bg">
      <img v-if="imageUrl" :src="imageUrl" class="avatar">
      <div v-if="imageUrl" class="hover_tip">重新上传</div>
      <i v-else class="el-icon-plus"></i>
    </div>

    <div v-show="bg">
      <img :src="imageUrl" width="250" height="180">
    </div> 
    
   
 
	</el-upload>


  
</div>

</template>


<script>
export default {
  props: {
    data: {
      type: String
    },
    bg: {
      type: String,
      default: ""
    },
    width: {
      default: "80px"
    },
    height: {
      default: "80px"
    },
  },
  data() {
    return {
      fileList: [], // 上传的文件列表
      url: process.env.BASE_API.split("/agent")[0] + "/admin/Common/uploadimg", // 上传图片地址
      imageUrl: "",
      limit: 1, // 限制数目
      upLoadData: {} // 上传时附带的额外参数
    };
  },
  created() { 
      if (this.data) {   
        this.imageUrl = this.data;
      } else if (this.bg) {
        let bg = require("@/assets/image/" + this.bg + ".png");
        this.imageUrl = bg;
      } 
  },
  methods: {
    //上传成功刷新并通知父组件
    handleAvatarSuccess(res, file) {
      this.loading = false;
      /* 预览 */
      this.imageUrl = URL.createObjectURL(file.raw);
      this.$emit("onSuccess", res.src);
      this.$message({
        message: "图片上传成功",
        type: "success"
      });
    },
    //图片上传前格式和大小检测
    beforeAvatarUpload(file) {
      const isImg =
        file.type === "image/jpeg" ||
        file.type === "image/gif" ||
        file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isImg) {
        this.$message.error("上传图片只能是 jpg/gif/png 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 2MB!");
      }
      if (isImg && isLt2M) {
        this.loading = true;
      }
      return isImg && isLt2M;
    }
  }
};
</script>

<style>
.avatar-uploader .el-upload { 
  width: 100%;
  height: 100%;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.el-icon-plus{
  font-size: 28px;
  color: #8c939d; 
  text-align: center;
}
.avatar {
  position: relative;
  width: 80px;
  height: 80px;
  display: block;
}
.hover_tip {
  position: absolute;
  bottom: 0;
  width: 100%;
  color: #fff;
  background: rgba(0, 0, 0, 0.3);
  height: 22px;
  font-size: 12px;
  padding: 0;
  line-height: 22px;
  text-align: center;
}
</style>



