<template>
	<div class="loaction"> 

    <el-select placeholder="区域" 
                v-model="region" 
                @change="showestate" 
                style="width: 200px;">
        <el-option v-for="item in data" 
                    :key="item.id"
                    :value="item.id"
                    :label="item.areaname">

        </el-option>

    </el-select>

    <el-select placeholder="地段" 
               v-model="estate"
               @change="emitChange()" 
                style="width: 200px">
        <el-option  v-for="item in estates" 
                    :key="item.id"
                    :value="item.id"
                    :label="item.areaname">
        </el-option>

    </el-select> 
 
  </div>
</template>
<script type="text/javascript">
import { GetRegion } from "@/api/public";
export default {
  components: {},
  props: {
    pregion: {
      type: Number, 
    },
    pestate: {
      type: Number, 
    }, 
  },
  data() {
    return {
      data: [],
      region: this.pregion,
      estates: [{ name: "区域" }],
      estate: this.pestate,
      districts: [{ name: "地段" }], 
    };
  },
  created() { 
    this.showregion();
  },
  methods: {
    showregion() {
      GetRegion().then(res => {
        if (res.data.code == 1) {
          this.data = res.data.data;

          if (this.region) { 
            this.getestate(this.region);
          }
        }
      });
    },
    getestate(val) {
      let obj = {};
      obj = this.data.find(item => {
        return item.id === val;
      });
      if (obj) {
        this.estates = obj.son;
      }
    },
    showestate(val) {
      this.estate = null;
      this.getestate(val);
      this.emitChange();
    },
    /* 传给父层 */
    emitChange() {
      this.$emit("emitChange", {
        region: this.region,
        estate: this.estate
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>   




