import request from "@/utils/request";

export function editCompanyInfo(data) {
  return request({
    url: "/broker/editCompanyInfo",
    method: "post",
    data
  });
}
export function getCompanyInfo(data) {
    return request({
      url: "/broker/companyInfo",
      method: "get",
    });
  }

export function companyMember(params) {
  return request({
    url: "/broker/companyMember",
    method: "get",
    params
  });
}

export function addCompantMember(data) {
  return request({
    url: "/broker/addCompantMember",
    method: "post",
    data
  });
}
export function removeCompany(id) {
  return request({
    url: "/broker/removeCompany",
    method: "get",
    params: {
      id: id
    }
  });
}
export function changeShopowner(data) {
  return request({
    url: "/broker/changeShopowner",
    method: "post",
    data
  });
}

 


